.BrainLeaderboard {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  height: 1px;
}
.BrainLeaderboard__panelHeader {
  display: flex;
  justify-content: center;
  align-items: center;
}
.BrainLeaderboard__panelHeader--icon {
  margin-top: 2px;
}
.BrainLeaderboard--icon {
  color: var(--button_primary_background);
  transition: transform linear 200ms;
}
.BrainLeaderboard a {
  color: inherit;
  outline: none;
  text-decoration: none;
}
.BrainLeaderboard__leader--badge {
  position: absolute;
  right: -2px;
  bottom: -2px;
  border-radius: 50%;
  border: 2px solid var(--modal_card_background);
  width: 16px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}
